import '../../src/jtForm/jtForm.ts'
$(document).ready(function () {
    $('#root').jtForm({map:[
        {
            name:'username',
            type:'text',
            label:':',
            rules:{
                type:'string',
                required:true,
                message:'',
            }
        },
        {
            name:'password',
            type:'text',
            fieldProps:{
              type:'password'
            },
            label:':',
            rules:{
                type:'string',
                required:true,
                message:'',
            }
        },
        [{
            name:'nickname',
            type:'text',
            fieldProps:{
                type:'text'
            },
            label:':',
            layout:{label:0.2,field:0.8},
            rules:{
                type:'string',
                message:'',
                pattern:/^\w*$/,
            }
        },
            {
                name:'email',
                type:'text',
                fieldProps:{
                    type:'text'
                },
                label:'email:',
                layout:{label:0.15,field:0.8},
                rules:{
                    type:'string',
                    message:'',
                    pattern:/^\w+@\w+\.\w+$/,
                }
            }
        ],

        {
            name:'imageFile',
            type:'file',
            fieldProps:{
                accept:'image/*'
            },
            label:':',
            rules:{
                required:true,
                message:'',
            }
        },
        {
            name:'cropperImage',
            type:'image',

            label:'',
            rules:{
                required:true,
                message:'',
            }
        },
    ]})
})
